.App {
  font-family: 'Open Sans', sans-serif;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
}

.App-header {
  margin-top:100px;
  text-align:center;
}

h1 {
  color:rgba(69, 69, 69, 1);
  line-height:2em;
}

a.button{
  display:inline-block;

  font:normal normal 300 1.3em 'Open Sans';
  text-decoration:none;

  color:rgba(28, 190, 131, 1);
  brackground-color:transparent;
  border:1px solid rgba(28, 190, 131, 1);
  border-radius:100px;

  padding: .3em 1.2em;
  margin:5px;

  background-size: 200% 100%;
  background-image: -webkit-gradient(linear, left top, right top, color-stop(50%, transparent), color-stop(50%, rgba(28, 190, 131, 1)));
  background-image: -webkit-linear-gradient(left, transparent 50%, rgba(28, 190, 131, 1) 50%);
  background-image: -o-linear-gradient(left, transparent 50%, rgba(28, 190, 131, 1) 50%);
  background-image: linear-gradient(to right, transparent 50%, rgba(28, 190, 131, 1) 50%);
  -webkit-transition: background-position .3s cubic-bezier(0.19, 1, 0.22, 1) .1s, color .5s ease 0s, background-color .5s ease;
  -o-transition: background-position .3s cubic-bezier(0.19, 1, 0.22, 1) .1s, color .5s ease 0s, background-color .5s ease;
  transition: background-position .3s cubic-bezier(0.19, 1, 0.22, 1) .1s, color .5s ease 0s, background-color .5s ease;
}

a.button:hover{
  color:rgba(255, 255, 255, 1);
  background-color:rgba(28, 190, 131, 1);
  background-position: -100% 100%;
}


